@import url('/components/home/files/jost.ttf');

* {
    margin: 0;
    padding: 0;
    font-family: 'Jost', serif;
    font-weight: normal;
    background-image: url(components/home/files/Background.jpg);
    background-repeat: repeat;
    color: rgb(66, 66, 66);

    -ms-touch-action: manipulation;
    touch-action: manipulation;
}

input, textarea, button, select, a {
    -webkit-tap-highlight-color: rgba(0, 0, 0, 0); /* Убрать подсвечивание ссылок */
}

//$breakpoints: (
//        mobile : 0px,
//        tablet : 600px,
//        desktop: 1200px
//);

@mixin for-phone-only {
    @media (max-width: 599px) {
        @content;
    }
}
@mixin for-tablet-portrait-up {
    @media (min-width: 600px) and (max-width: 1199px) {
        @content;
    }
}
@mixin for-tablet-landscape-up {
    @media (min-width: 900px) and (max-width: 1199px) {
        @content;
    }
}
@mixin for-desktop-up {
    @media (min-width: 1200px) {
        @content;
    }
}
@mixin for-big-desktop-up {
    @media (min-width: 1800px) {
        @content;
    }
}

/** header **/
.Header {
    text-align: center;
    outline: none;
    user-select: none;
    padding: 0.7em;
    @mixin for-phone-only {
        margin: 0.3em auto;
        width: 80%;
    }

    @mixin for-phone-only {

    }
    @mixin for-tablet-portrait-up {

    }
    @mixin for-tablet-landscape-up {

    }
    @mixin for-desktop-up {

    }
    @mixin for-big-desktop-up {

    }
}
.HeaderButton {
    display: inline-block;
    width: 10em;
    @mixin for-phone-only {
        margin-top: 0.3em;
        width: 7em;
    }
}
.Header a {
    letter-spacing: 1px;
    text-decoration: none;
    font-size: 1.3em;
    @mixin for-phone-only {
        font-size: 1.1em;
    }
}
.HeaderButton a {
    padding-bottom: 5px;
    background: linear-gradient(#565656, #565656) no-repeat center bottom;
    background-size: 0 2px;
    transition: all 0.15s;
}
.HeaderButton a:hover {
    background-size: 100% 2px;
    @mixin for-phone-only {
        font-weight: normal;
    }
}
.HeaderBackButton a {
    padding-bottom: 5px;
    display: inline-block;
    background: linear-gradient(#565656, #565656) no-repeat center bottom;
    background-size: 0 2px;
    transition: all 0.1s;
}
.HeaderBackButton a:hover {
    background-size: 100% 2px;
}

/** *** *** *** *** **/
/**    HOMEPAGE     **/
/** *** *** *** *** **/

/** face **/
.Face {
    text-align: center;
    margin-top: 10%;
    @mixin for-phone-only {
        margin: 2em 1.5em 0 1.5em;
    }
}
.Face h1 {
    font-size: 2.5em;

    @mixin for-phone-only {
        font-size: 2.5em;
    }
}
.Face h2 {
    font-weight: normal;
    @mixin for-phone-only {
        margin: 1em 0 0 0.7em;
        font-size: 1.5em;
    }
}
.reactIcon {
    text-align: center;
    margin: 2em auto 0 auto;
    background: rgba(0, 0, 0, 0.005);
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    text-rendering: optimizeLegibility;
    text-shadow: 1px 1px 1px rgba(0, 0, 0, 0.004);
    animation-name: spin;
    animation-duration: 20s;
    animation-iteration-count: infinite;
    animation-timing-function: linear;
}
@keyframes spin {
    from {
        transform: rotate(0deg);
    }
    to {
        transform: rotate(360deg);
    }
}

/** about **/
.About {
    text-align: center;
    @include for-phone-only {
        margin: 1em 1em 1em 1em;
    }
    @include for-tablet-portrait-up {
        margin: 1em 2em 1em 3em;
    }
    @include for-tablet-landscape-up {
        width: 70%;
        margin: 0 auto;
    }
    @include for-desktop-up {
        width: 70%;
        margin: 0 auto;
    }
    @include for-big-desktop-up {
        width: 70%;
        margin: 0 auto;
    }
}
.About h1 {
    text-align: center;
    @include for-phone-only {
        font-size: 2.5em;
    }
    @include for-tablet-landscape-up {
        margin-top: 2em;
        font-size: 2.5em;
        margin-bottom: 1em;
    }
    @include for-desktop-up {
        margin-top: 4em;
        font-size: 2.5em;
        margin-bottom: 1em;
    }
    @include for-big-desktop-up {
        margin-top: 6em;
        font-size: 2.5em;
        margin-bottom: 1em;
    }
