/** technologies **/
.Technologies {
    text-align: center;
    margin: 6em auto 2em auto;
}
.Technologies h1 {
    font-size: 2.5em;
}
.TechnologiesIconPlaceHolder {
    margin: 3em auto 0 auto;
    text-align: center;
}
.TechnologiesIcon {
    transform: scale(1.3);
    transition-delay: 0s;
    transition-duration: 0.15s;
    transition-property: all;
    transition-timing-function: ease;
    display: inline-block;
    margin: 0 2em 0 2em;
    background: rgba(0, 0, 0, 0.005);
}
.TechnologiesIcon:hover .Popup {
    display: block;
}
.TechnologiesIcon:hover {
    transform: scale(1.45);
}
.Popup {
    font-size: 0.9em;
    position: absolute;
    width: 7em;
    margin: 0 auto;
    transform: translateX(-2.05em);
    display: none;
    border-radius: 2em;
    padding: 0.3em;
    box-shadow: rgb(211, 211, 211) 1px 1px 1px;
    background: rgb(255, 255, 255);
    border: 0.1em solid rgba(75, 71, 71, 0.42);
}

/** cards **/
.Card {
    box-shadow: rgb(211, 211, 211) 2px 2px 30px;
    font-family: "Cormorant Garamond", serif;
    text-align: center;
    transition-delay: 0s;
    transition-duration: 0.1s;
    transition-property: all;
    transition-timing-function: ease;
    border-radius: 2em;
    text-decoration: none;
    background: white;
    border: 1px solid rgba(75, 71, 71, 0.349);
    overflow: hidden;
    outline: none;
    user-select: none;
    -webkit-tap-highlight-color: transparent;
    display: inline-block;

    margin: 1em 0.5em 3em;

    width: 12em;
    height: 12em;
    @include for-phone-only {
        width: 12em;
        padding-bottom: 1em;
    }
    @include for-desktop-up {
        margin: 2em;
        width: 14em;
        height: 16em;
        border-radius: 2em;
    }

}
.Card:hover {
    @include for-desktop-up {
        transform: scale(1.03)
    }
}
.Card:focus img:nth-child(2) {
    display: none;
    @include for-phone-only {
        display: inline;
    }
    @include for-tablet-portrait-up {
        display: inline;
    }
    @include for-tablet-landscape-up {
        display: inline;
    }

}
.Card:focus img:nth-child(1) {
    display: inline;
    @include for-phone-only {
        display: none;
    }
    @include for-tablet-portrait-up {
        display: none;
    }
    @include for-tablet-landscape-up {
        display: none;
    }
}
.Card:hover img:nth-child(2) {
    @include for-desktop-up {
        display: inline;
    }
}
.Card:hover img:nth-child(1) {
    @include for-desktop-up {
        display: none;
    }
}
.Card:focus {
    transform: scale(0.85);
    transition-duration: 0.1s;
}
.Card:active {
    transform: scale(0.85);
    transition-duration: 0.1s;
}
.ProjectCard {
    @extend .Card;
    @include for-desktop-up {
        width: 28em;
        height: 13em;
    }
}
.Card img {
    margin: 1.5em;
    width: 30%;
    height: 30%;
    background: white;
    @include for-desktop-up {
        width: 42%;
        height: 32%;
    }
}
.ProjectCard img {
    margin: 1.5em;
    width: 30%;
    height: 30%;
    background: white;
    @include for-desktop-up {
        margin: 1.5em;
        width: 40%;
        height: 42%;
    }
}
.CardText {
    background: white;
    @include for-desktop-up {
        margin: 2px 16px;
    }
}
.Card h3 {
    background: white;
    font-size: 1.2em;
}
.Card h4 {
    background: white;
    font-size: 0.9em;
}
.Card img:nth-child(1) {
    display: inline;
    @include for-phone-only {
        display: none;
    }
    @include for-tablet-portrait-up {
        display: none;
    }
    @include for-tablet-landscape-up {
        display: none;
    }

}
.Card img:nth-child(2) {
    display: none;
    @include for-phone-only {
        display: inline;
    }
    @include for-tablet-portrait-up {
        display: inline;
    }
    @include for-tablet-landscape-up {
        display: inline;
    }
}

