/** *** *** *** *** **/
/**  WHEELCHECKER   **/
/** *** *** *** *** **/

/** description **/
.Description {
    text-align: center;
    text-decoration-line: none;
    font-size: 1.2em;
    margin-top: 2em;
    color: rgba(0, 0, 0, 0.66)
}

/** wheelcheckarea **/
.WheelCheckArea {

    @include for-phone-only {
        margin: 5em;
        height: 15em;
    }
    @include for-tablet-portrait-up {
        margin: 5em 10em 0 10em;
        height: 20em;
    }
    @include for-tablet-landscape-up {
        margin: 5em 10em 0 10em;
        height: 20em;
    }
    @include for-desktop-up {
        margin: 5em 10em 0 10em;
        height: 20em;
    }
    @include for-big-desktop-up {
        margin: auto;
        width: 60%;
        height: 45em;
    }
}

/** wheel **/
#Wheel {
    padding-top: 8em;
    content: url("components/mousewheelChecker/files/mouse.png");
    /*border-style: solid;*/
    position: relative;
    float: right;
    animation: moving 10s infinite;
    background: transparent;
    /*иногда появляется какой-то непонятный бордер вокруг прозрачной png. Строки снизу это фиксят*/
    filter: progid:DXImageTransform.Microsoft.gradient(startColorstr=#00FFFFFF, endColorstr=#00FFFFFF);
    zoom: 1;
    @include for-phone-only {
        width: 10em;
        margin: 0;
    }
    @include for-tablet-portrait-up {
        width: 10em;
        margin: 0;
    }
    @include for-tablet-landscape-up {
        width: 10em;
        margin: 0;
    }
    @include for-desktop-up {
        width: 10em;
        margin: 0 3em 0 3em;
    }
    @include for-big-desktop-up {
        width: 20em;
    }
}
@keyframes moving {
    0% {
        left: 0;
        transform: rotateY(0deg)
    }
    18% {
        left: -40%;
        transform: rotateY(0deg) scale(0.7)
    }
    32% {
        left: -60%;
        transform: rotateY(0deg) scale(0.8)
    }
    45.5% {
        left: -80%;
        transform: rotateY(0deg)
    }
    46% {
        left: -80%;
        transform: rotateY(180deg) scale(0.95)
    }
    55% {
        left: -60%;
        transform: rotateY(180deg) scale(0.7)
    }
    65% {
        left: -40%;
        transform: rotateY(180deg) scale(0.85)
    }
    74.6% {
        left: -15%;
        transform: rotateY(180deg) scale(0.7)
    }
    75% {
        left: -15%;
        transform: rotateY(180deg) scale(0.7)
    }
    87% {
        left: -5%;
        transform: rotateY(180deg) scale(0.9)
    }
    99.7% {
        left: 0;
        transform: rotateY(180deg) scale(1)
    }
    100% {
        left: 0;
        transform: rotateY(0deg) scale(1)
    }
}

/** WHEEL MOVEMENTS **/
#Wheel.Up {
    content: url("components/mousewheelChecker/files/mouseUp.png");
    /*transform: scale(1.05);*/
    filter: none;
}
#Wheel.Down {
    content: url("components/mousewheelChecker/files/mouseDown.png");
    /*transform: rotateY(180deg);*/
    filter: none;
}

